}

.sidebar .nav ul {
  list-style: none;
  margin-top: 53rem;
  margin-right: 3rem;
}

.sidebar nav ul li {
    position: relative;

    color: white;
    opacity: 70%;
  }

.sidebar nav ul li a {
      line-height: 3em;
      text-transform: uppercase;
      text-decoration: none;
      letter-spacing: 0.4em;
      color: white;

      text-align: left;
      transition: all ease-out 300ms;

